.menu {
  width: 100%;
  display: flex;
  justify-content: center;
  background: none;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(32, 32, 32, 0);
  z-index: 5;
  transition: all 1s ease;
}
